.Loader {
  grid-column-start: 1;
  grid-column-end: 3;
  align-self: center;
  justify-self: center;
  font-size: 10px;
  text-indent: -9999em;
  width: 11em;
  height: 11em;
  border-radius: 50%;
  background: rgb(131, 109, 168);
  background: -moz-linear-gradient(left, rgb(131, 109, 168) 10%, rgba(192,87,238, 0) 42%);
  background: -webkit-linear-gradient(left, rgb(131, 109, 168) 10%, rgba(192,87,238, 0) 42%);
  background: -o-linear-gradient(left, rgb(131, 109, 168) 10%, rgba(192,87,238, 0) 42%);
  background: -ms-linear-gradient(left, rgb(131, 109, 168) 10%, rgba(192,87,238, 0) 42%);
  background: linear-gradient(to right, rgb(131, 109, 168) 10%, rgba(192,87,238, 0) 42%);
  position: relative;
  -webkit-animation: load3 1.4s infinite linear;
  animation: load3 1.4s infinite linear;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
}

.Loader:before {
  width: 50%;
  height: 50%;
  background: rgb(131, 109, 168);
  border-radius: 100% 0 0 0;
  position: absolute;
  top: 0;
  left: 0;
  content: '';
}
.Loader:after {
  background: #ffffff;
  width: 75%;
  height: 75%;
  border-radius: 50%;
  content: '';
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

@-webkit-keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@media (max-width: 800px) {
  .Loader {
   position: absolute;
   top: 50vw;
  }

  .Loader:after {
    background: rgb(171, 231, 231);
  }
}